import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { DatepickerOptions } from "ng2-datepicker";
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from "../../service/authentication.service";
declare var $:any;
export interface PeriodicElement{
  id:string;
  framesname:string;
  image:string;
  price:string;
}
const ELEMENT_DATA:PeriodicElement[]= [
  { id:"123456", framesname:"Name", image:"Time Period", price:"25 Gold Coins" },
  { id:"123456", framesname:"Name", image:"Time Period", price:"25 Gold Coins" },
  { id:"123456", framesname:"Name", image:"Time Period", price:"25 Gold Coins" },
];


@Component({
  selector: 'app-frames',
  templateUrl: './frames.component.html',
  styleUrls: ['./frames.component.css']
})
export class FramesComponent implements OnInit {
  displayedColumns: string[] = [
    "checkbox",
    "id",
    "framesname",
    "image",
    "cointype",
    "price",    
    "action",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  fromdate: any;
  todate: Date;
  errorMessage: any;
  id: any;  
  single: any[];
  picture: any;  
  optionsTo: { minDate: Date; maxDate: Date };
  optionsFrom: DatepickerOptions = {
    minYear: 2017,
    maxDate: new Date(),
  };
  editForm : any
  addForm : any
  submitted : boolean = false
  allPost : any = []
  constructor(
    private FormBuilder: FormBuilder,    
    // private api: AuthenticationService,
    private router: Router,
    private route: ActivatedRoute,
    private toast : ToastrService,
    private api:AuthenticationService,
    private service: AuthenticationService,
  ) {
    this.fromdate = new Date();
    this.todate = new Date();
  }

  ngOnInit() {
   
    this.addForm = this.FormBuilder.group({
      title: ["", [Validators.required]],
      coinType: ["0", [Validators.required]],
      price: ["", [Validators.required]]
    })

    this.editForm = this.FormBuilder.group({
      title: ["", [Validators.required]],
      coinType: ["0", [Validators.required]],
      price: ["", [Validators.required]]
    })
    this.getAllFrames()
  }

  getAllFrames(){
    this.api.getAllFrame().subscribe( ( result ) => {
      console.log(result)
      this.allPost = result['response'].reverse()
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.allPost);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }

  get f() {
    return this.addForm.controls;
  }

  get g() {
    return this.editForm.controls;
  }

  selImage
  singleFilesForPackage(event) {
    this.selImage =true
      const file1 = event.target.files[0];
      this.single = [];
      var singleFiles = event.target.files;
      if (singleFiles) {
        for (var file of singleFiles) {
          var singleReader = new FileReader();
          singleReader.onload = (e: any) => {
            this.single.push(e.target.result);
            document.getElementById("imagefile1").setAttribute("src", e.target.result);
          };
          singleReader.readAsDataURL(file);
        }
      }
      this.picture = event.target.files[0];
    }


    singleFilesForPackageUpdate(event) {
      this.selImage =true
        const file1 = event.target.files[0];
        
        this.single = [];
        var singleFiles = event.target.files;
        if (singleFiles) {
          for (var file of singleFiles) {
            var singleReader = new FileReader();
            singleReader.onload = (e: any) => {
              this.single.push(e.target.result);
              document.getElementById("imagefile2").setAttribute("src", e.target.result);  
            };
            singleReader.readAsDataURL(file);
          }
        }
        this.picture = event.target.files[0];
      }

    add(){
      this.submitted = true
      if(this.addForm.invalid) return
      let datatosubmit = new FormData()
      datatosubmit.append("title",this.addForm.value.title)
      datatosubmit.append("price",this.addForm.value.price)
      datatosubmit.append("coinType",this.addForm.value.coinType)
      datatosubmit.append("frameImages",this.picture)

      this.api.addFrames(datatosubmit).subscribe( ( result ) => {
        this.getAllFrames()
        this.service.toaster("success", "frame added Successfully");
        $('#framesadd').modal('hide');
      })
    }

    edit(){
      this.submitted = true
      if(this.editForm.invalid) return
      let datatosubmit = new FormData()
      datatosubmit.append("_id",this.id)
      datatosubmit.append("title",this.editForm.value.title)
      datatosubmit.append("price",this.editForm.value.price)
      datatosubmit.append("coinType",this.editForm.value.coinType)
      if(this.picture){
        datatosubmit.append("frameImages",this.picture)
      }
      
      this.api.editFrames(datatosubmit).subscribe( ( result ) => {
        this.getAllFrames()
        this.service.toaster("success", "frame updated Successfully");
        $('#framesedit').modal('hide');
      })
    }

    showeditpopup(ele){
      this.submitted = false
      this.id = ele._id
      this.editForm.patchValue({
        title:ele.title,
        coinType :ele.coinType.toString(),
        price :ele.price,
      })
    }

    showaddpopup(){
      this.addForm.reset()
      this.addForm.patchValue({coinType:"0"})
      this.submitted = false
    }

    sortfilter(event){
      let sort = event.target.value;
      console.log(sort,"hiiii inside sort")
      if(sort === "0"){
        console.log(this.allPost,"userss")
         this.allPost.sort((a,b) => (a.title > b.title) ? 1 : ((b.title > a.title) ? -1 : 0));
         this.dataSource = new MatTableDataSource<PeriodicElement>(this.allPost);
         this.dataSource.paginator = this.paginator;
      }else{
       this.allPost.sort((a,b) => (a.title < b.title) ? 1 : ((b.title < a.title) ? -1 : 0));
       this.dataSource = new MatTableDataSource<PeriodicElement>(this.allPost);
       this.dataSource.paginator = this.paginator;
      }
   }

   blockUnblock(id){
    this.id = id
  }

  pickupID(id){
    this.id = id
  }

  blockunblockuser(){
    this.api.blockUnblockFrames({
      _id:this.id
    }).subscribe( ( result ) => {
      this.getAllFrames()
    } )
  }

  delUser(){
    this.api.removeFrames({
      _id:this.id
    }).subscribe( ( result ) => {
      this.getAllFrames()
    } )
  }

}
