import { Component, OnInit, ViewChild } from '@angular/core';
import { getMatIconFailedToSanitizeUrlError, MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { DatepickerOptions } from "ng2-datepicker";
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from "../../service/authentication.service";
declare var $:any;
export interface PeriodicElement{
  sid:string;
  combinationname:string;
  combinationtype:string;
  priceingold:string;
}
const ELEMENT_DATA:PeriodicElement[]= [
  { sid:"101", combinationname:"Top", combinationtype:"1", priceingold:"Price" },
  { sid:"101", combinationname:"Five Digit", combinationtype:"1", priceingold:"Price" },
  { sid:"101", combinationname:"Paris", combinationtype:"7", priceingold:"Price" },
];

@Component({
  selector: 'app-unique-user',
  templateUrl: './unique-user.component.html',
  styleUrls: ['./unique-user.component.css']
})
export class UniqueUserComponent implements OnInit {

  displayedColumns: string[] = [
    "checkbox",
    "sid",
    "combinationname",
    "combinationtype",
    "combinationSubType",
    "priceingold",    
    "action",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  fromdate: any;
  todate: Date;
  errorMessage: any;
  id: any;  
  single: any[];
  picture: any;  
  addForm : FormGroup;
  editForm : FormGroup;
  submitted : boolean = false
  esubmitted : boolean = false
  selectedType : any = ""
  selectedSubType : any = ""
  selectedTypeEdit : any = ""
  selectedSubTypeEdit : any = ""
  packageSetId : any = ""
  generatedIds : any = []
  idSetList : any = []
  categories : any = [
    {id : 1, name:"5 digits", subcategory : []},
    {id : 2, name:"Pairs", subcategory : [{id:1,name:'ABABA'},{id:2,name:"AAABA"},{id:3,name:"AAACBAA"}]},
    {id : 3, name:"Repeating", subcategory : [{id:1,name:'AAAA'},{id:2,name:"AAAAA"},{id:3,name:"AAAAAA"}]},
    {id : 4, name:"Consecutive", subcategory : [{id:1,name:'ABCD'},{id:2,name:"ABCDE"},{id:3,name:"ABCDEF"}]},
  ]

  subcategories : any = []  

  optionsTo: { minDate: Date; maxDate: Date };
  optionsFrom: DatepickerOptions = {
    minYear: 2017,
    maxDate: new Date(),
  };


  constructor(
    private formBuilder: FormBuilder,    
    private service: AuthenticationService,
    private router: Router,
    private route: ActivatedRoute,
    private toast : ToastrService
  ) {
    this.fromdate = new Date();
    this.todate = new Date();
   }

  ngOnInit() {

    this.addForm = this.formBuilder.group({
      type: ["", [Validators.required]],
      subType: [""],
      price: [0, [Validators.required, Validators.pattern]],
      name :["", [ Validators.required,Validators.pattern]]
    });

    this.editForm = this.formBuilder.group({
      price: [0, [Validators.required, Validators.pattern]],
      name :["", [ Validators.required,Validators.pattern]]
    });

    this.getIdSet()

  }

  getIdSet(){
    this.service.getUniqueIds(1).subscribe((res)=>{
      this.idSetList = res.response
      this.idSetList.filter((ele)=>{
        ele.typeDetails = this.categories[this.categories.findIndex(obj => obj.id == ele.type)]
        ele.subTypeDetails = ele.typeDetails.subcategory.findIndex(obj => obj.id == ele.subType)
        console.log(ele.subTypeDetails)
        if( ele.subTypeDetails >= 0  ){
          ele.subTypeDetails = ele.typeDetails.subcategory[ele.subTypeDetails]
        }else{
          ele.subTypeDetails = {}
        }
      })
      console.log(this.idSetList)
      this.dataSource = new MatTableDataSource(this.idSetList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    },
    (err)=>{
      console.log(err)
    })
  }

  changeMainCategory(catid){
    this.subcategories =  this.categories.find(obj => obj.id == catid);
    this.generatedIds = []
    console.log(this.subcategories)
    this.subcategories =  this.subcategories.subcategory
  }

  changeSubCategory(){
    this.generatedIds = []
  }

  generate(){
    let dataToSend = {
      "type":this.selectedType,
      "subType":this.selectedSubType,
      "for_user":1
  }
    this.service.generateId(dataToSend).subscribe((res)=>{
      let gids : any = res
      console.log(gids.response)
      this.generatedIds = this.generatedIds.concat(gids.response)
      this.generatedIds = this.generatedIds.filter((item, index) => this.generatedIds.indexOf(item) === index);
      console.log(this.generatedIds)
    },(err)=>{
      console.log(err)
    })
  }

  showaddpop(){
    this.submitted = false
    this.generatedIds = []
    $("#themesadd").modal("show")
    this.addForm.patchValue({
      type: "",
      subType: "",
      price: 0,
      name : ""
    })
  }

  showeditpop(ele){
    this.esubmitted = false
    $("#themesedit").modal("show")
    this.editForm.patchValue({
      price: ele.price,
      name : ele.name
    })
    this.selectedTypeEdit = ele.type 
    this.packageSetId = ele._id
    this.changeMainCategory(ele.type)
    this.selectedSubTypeEdit = ele.subType 
    this.generatedIds = ele.idNumbers
  }

  get f() {
    return this.addForm.controls;
  }

  get g() {
    return this.editForm.controls;
  }

  submitaddform(){
    this.submitted = true
    if(this.addForm.invalid){ return }
    if(!this.generatedIds.length){
      this.toast.warning("Please generate ids before adding")
      return
    }
    let dataToSubmit = this.addForm.value
    dataToSubmit.idNumber = this.generatedIds
    dataToSubmit.for_user = 1
    console.log(dataToSubmit)
    this.service.adduniqueid(dataToSubmit).subscribe((res)=>{
      this.toast.success("Unique Id set created successfully")
      $("#themesadd").modal("hide")
      this.getIdSet()
    },(err) => {
      console.log(err)
    })
  }

  submiteditform(){
    this.esubmitted = true
    if(this.editForm.invalid){ return }
    let dataToSubmit = this.editForm.value
    dataToSubmit.packageSetId = this.packageSetId
    this.service.editUniqueId(dataToSubmit).subscribe(
      (res) => {
        this.toast.success("Unique Id set updated successfully")
        $("#themesedit").modal("hide")
        this.getIdSet()
      },
      (err) => {
        console.log(err)
      }
    )
  }

  pickupID(id){
    this.id = id
  }

  blockunblockuser(status){
    let dataToSet = {status:status,packageSetId:this.id}
    this.service.blockUnblockUniqueId(dataToSet).subscribe(
      (res) => {
        this.toast.success("Unique Id set status changed successfully")
        $("#blockUser").modal("hide")
        $("#unblockUser").modal("hide")
        this.getIdSet()
      },
      (err) => {
        console.log(err)
      }
    )
  }

  delUser(status){
    let dataToSet = {packageSetId:this.id}
    this.service.deleteUniqueId(dataToSet).subscribe(
      (res) => {
        this.toast.success("Unique Id set deleted successfully")
        $("#deleteUser").modal("hide")
        this.getIdSet()
      },
      (err) => {
        console.log(err)
      }
    )
  }
  // selImage
  // singleFilesForPackage(event) {
  //   this.selImage =true
  //     const file1 = event.target.files[0];
  //     this.single = [];
  //     var singleFiles = event.target.files;
  //     if (singleFiles) {
  //       for (var file of singleFiles) {
  //         var singleReader = new FileReader();
  //         singleReader.onload = (e: any) => {
  //           this.single.push(e.target.result);
  //           document.getElementById("imagefile1").setAttribute("src", e.target.result);
  //           document.getElementById("imagefile2").setAttribute("src", e.target.result);  
  //         };
  //         singleReader.readAsDataURL(file);
  //       }
  //     }
  //     this.picture = event.target.files[0];
  //   }

}
