import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { DatepickerOptions } from "ng2-datepicker";
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from "../../service/authentication.service";
declare var $:any;
export interface PeriodicElement{
  sid:string;
  pkgname:string;
  duration:string;
  priceingold:string;
}
const ELEMENT_DATA:PeriodicElement[]= [
  { sid:"101", pkgname:"Name", duration:"Time Period", priceingold:"Price" },
  { sid:"101", pkgname:"Name", duration:"Time Period", priceingold:"Price" },
  { sid:"101", pkgname:"Name", duration:"Time Period", priceingold:"Price" },
];

@Component({
  selector: 'app-room-lock',
  templateUrl: './room-lock.component.html',
  styleUrls: ['./room-lock.component.css']
})
export class RoomLockComponent implements OnInit {
  displayedColumns: string[] = [
    "checkbox",
    "sid",
    "pkgname",
    "duration",
    "priceingold",    
    "action",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);



  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  fromdate: any;
  todate: Date;
  errorMessage: any;
  id: any;  
  single: any[];
  picture: any;  
  allPost : any = [];
  submitted : boolean = false;
  optionsTo: { minDate: Date; maxDate: Date };
  optionsFrom: DatepickerOptions = {
    minYear: 2017,
    maxDate: new Date(),
  };
  addForm : FormGroup
  editForm : FormGroup
  durationFilter : any = ""
  nameFilter : any = ""
  constructor(   
    // private api: AuthenticationService,
    private router: Router,
    private route: ActivatedRoute,
    private toast : ToastrService,
    private api:AuthenticationService,
    private FormBuilder: FormBuilder,
    private service: AuthenticationService,
  ) {
    this.fromdate = new Date();
    this.todate = new Date();
   }

  ngOnInit() {
    this.getAllPackages()

    this.addForm = this.FormBuilder.group({
      packageName: ["", [Validators.required]],
      duration: ["1", [Validators.required]],
      price: ["", [Validators.required, Validators.pattern(`^[0-9]+$`)]]
    })

    this.editForm = this.FormBuilder.group({
      packageName: ["", [Validators.required]],
      duration: ["1", [Validators.required]],
      price: ["", [Validators.required, Validators.pattern(`^[0-9]+$`)]]
    })

  }

  get f() {
    return this.addForm.controls;
  }

  get g() {
    return this.editForm.controls;
  }
  
  savePackage(){
    this.submitted = true
    if (this.addForm.invalid) {
      return
    }
    
  
    let dataTosave =   {
      "title":this.addForm.value.packageName,
      "price":this.addForm.value.price,
      "month":this.addForm.value.duration,
    }

    this.api.addAllRoomLockPackage(dataTosave).subscribe( ( result ) => {
      this.getAllPackages()
      this.service.toaster("success", "package added Successfully");
      $('#roomlockadd').modal('hide');
    })
  }

  updatePackage(){
    this.submitted = true
    if (this.editForm.invalid) {
      return
    }
    
    let dataTosave =   {
      "_id":this.id,
      "title":this.editForm.value.packageName,
      "price":this.editForm.value.price,
      "month":this.editForm.value.duration,
    }
    this.api.editAllRoomLockPackage(dataTosave).subscribe( ( result ) => {
      this.getAllPackages()
      this.service.toaster("success", "package updated Successfully");
      $('#roomlockedit').modal('hide');
    })
  }

  showAddPopup(){
    this.submitted = false
    this.addForm.patchValue({
      "packageName":"",
      "price":"",
      "duration":"1"
    });

   
  }

  showEditPopup(ele){
    this.submitted = false
    this.id = ele._id
    console.log(ele)
    this.editForm.patchValue({
      "packageName":ele.title,
      "price":ele.price,
      "duration":ele.month
    });

    
  }

  getAllPackages() {
    this.api.getAllRoomLockPackage().subscribe( ( result ) => {
      this.allPost = result['data'].reverse()
      console.log(this.allPost)
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.allPost);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  selImage
  // singleFilesForPackage(event) {
  //   this.selImage =true
  //     const file1 = event.target.files[0];
  //     this.single = [];
  //     var singleFiles = event.target.files;
  //     if (singleFiles) {
  //       for (var file of singleFiles) {
  //         var singleReader = new FileReader();
  //         singleReader.onload = (e: any) => {
  //           this.single.push(e.target.result);
  //           document.getElementById("imagefile1").setAttribute("src", e.target.result);
  //           document.getElementById("imagefile2").setAttribute("src", e.target.result);  
  //         };
  //         singleReader.readAsDataURL(file);
  //       }
  //     }
  //     this.picture = event.target.files[0];
  //   }

    blockUnblock(id){
      this.id = id
    }

    pickupID(id){
      this.id = id
    }

    blockunblockuser(){
      this.api.blockUnblockAllRoomLockPackage({
        _id:this.id
      }).subscribe( ( result ) => {
        this.getAllPackages()
      } )
    }

    delUser(){
      this.api.deleteAllRoomLockPackage({
        _id:this.id
      }).subscribe( ( result ) => {
        this.getAllPackages()
      } )
    }

    doFilter(){
      let allPost = this.allPost
      if(this.nameFilter){
        allPost = this.filterItems(this.nameFilter,allPost)
      }
      if(this.durationFilter){
        allPost = allPost.filter((ele)=>{ if(ele.month==this.durationFilter){ return ele }})
      }

      this.dataSource = new MatTableDataSource<PeriodicElement>(allPost);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    filterItems = (needle, heystack) => {
      let query = needle.toLowerCase();
      return heystack.filter(item => item.title.toLowerCase().indexOf(query) >= 0);
    }

}
